@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap');

@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.0/css/all.min.css');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins';
}

.header {
    padding: 1.3em 2em;
    background-color: #121212;
}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.logo-text {
    color: #FF9A36;
}

a {
    text-decoration: none;
    color: #fff;
}

.nav-button {
    padding: .6em 1em;
    border-radius: .3em;
    margin: 0 .6em;
    border: 2px solid #FF9A36;
}

.fill {
    background-color: #FF9A36;
    color: #fff;
}

.list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 1em;
}

.list-item a {
    margin: 0 1em;
}

.mobile-menu {
    display: none;
}

i {
    color: #fff;
    font-size: x-large;
}

@media (min-width: 260px) and (max-width: 829px) {
    
    .mobile-menu {
        display: block;
    }

    .nav-list {
        display: none;
    }

    .buttons-holder {
        display: none;
    }

    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .header {
        padding: 1.3em 1.2em;
    }

}