body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-bar {
  background-color: rgba(144, 144, 144, .8);
  backdrop-filter: blur(20px);
  border-radius: .3em;
  width: 60%;
  padding: 1em 1.2em;
  margin: 2em .6em 0 0;
  border: none;
  outline: none;
  color: #fff;
}

.search-button {
  padding: 1em 2em;
  border-radius: .4em;
  border: none;
  outline: none;
  background-color: #FF9A36;
  color: #fff;
  cursor: pointer;
}

.form {
  text-align: center;
  background-color: #181818;
}


@media (min-width: 260px) and (max-width: 950px) {
  .form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
  }

  .search-button {
    padding: 1em 2em;
    border-radius: .4em;
    border: none;
    outline: none;
    background-color: #FF9A36;
    color: #fff;
    cursor: pointer;
    margin: .7em 0;
  }
}