@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap');

.recipe-box {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    background-color: #2c2c2c;
    margin: 1em 1em;
    border-radius: .4em;
    padding: 1.2em 1.3em;
    font-family: 'Poppins';
    color: #fff;
}

.box-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.food-img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-size: cover;
} 

.food-label {
    margin-left: .9em;
    font-size: x-large;
}

.recipe-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    text-align: left;
}

.list-item {
    padding: 1em 0;
    margin: 2em 2em 0 1em;
}

.box-footer {
    margin: 1em 0 0 0;
}

@media (min-width: 260px) and (max-width: 410px) {
    .recipe-box {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: flex-start;
        background-color: #2c2c2c;
        margin: 1em 0;
        border-radius: .4em;
        padding: 1.2em 1.3em;
        font-family: 'Poppins';
        color: #fff;
    }
    .box-header {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        text-align: center;
    }
    .food-img {
        width: 75px;
        height: 75px;
        border-radius: 50%;
        background-size: cover;
    } 
    
    .food-label {
        margin-top: .9em;
        font-size: larger;
    }
    
    .recipe-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: flex-start;
        text-align: left;
    }
    
    .list-item {
        padding: 1em 0;
        margin: 2em 2em 0 1em;
    }
    
    .box-footer {
        margin: 1em 0 0 0;
    }
    
}